<template>
  <div class="component-home" ref="rootRef">
    <nav>
      <ul>
        <li v-for="(n, idx) in state.navs" :class="{ active: n.active }" @click="move(n.name)" :key="idx">{{ n.name.substring(0, 1).toUpperCase() + n.name.substring(1) }}</li>
      </ul>
      <button class="btn" @click="callback('toggle-console')" :title="`${visible ? 'Close' : 'Open'} Console`">
        <span v-if="visible">CLOSE </span>
        <span v-else>OPEN </span>
        <span>CONSOLE</span>
      </button>
    </nav>
    <header>
      <div class="inner">
        <h1 class="font-parisienne color-point">Publessing</h1>
        <br />
        <h2 class="font-parisienne color-point">
          <b>Draw the UI with commands</b>
        </h2>
        <template v-if="login">
          <h3>
            Enter "<b>create startup</b>" <br />
            then "<b>open startup</b>"
          </h3>
        </template>
        <template v-else>
          <h3>
            Enter "<b>join</b>" to create account <br />
            or "<b>login</b>" to use the service
          </h3>
        </template>
        <button class="btn btn-point" @click="callback('toggle-console')" :title="`${visible ? 'Close' : 'Open'} Console`">
          <span v-if="visible">CLOSE </span>
          <span v-else>OPEN </span>
          <span>CONSOLE</span>
        </button>
      </div>
    </header>
    <article>
      <div class="guide wrapper">
        <div class="title"># Service Guide</div>
        <div class="content">
          <h1>Get Started</h1>
          <ul>
            <li>최상단의 <b>OPEN CONSOLE</b> 버튼을 클릭합니다.</li>
            <li>명령창에 <b>join</b> 입력 후 엔터를 눌러 회원가입을 진행합니다.</li>
            <li>명령창에 <b>login</b> 입력 후 엔터를 눌러 로그인합니다.</li>
            <li>명령창에 <b>create 이름</b> 입력 후 엔터를 눌러 프로젝트를 생성합니다.</li>
            <li>명령창에 <b>open 이름</b> 입력 후 엔터를 눌러 프로젝트를 오픈합니다.</li>
          </ul>
          <h1>Command List</h1>
          <div class="block">
            <h2>콘솔 명령어</h2>
            <table class="table table-bordered">
              <thead>
                <tr>
                  <th>Command</th>
                  <th>Description</th>
                  <th>Arguments</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(c, idx) in commands.console" :key="idx">
                  <td>{{ c.cmd }}</td>
                  <td>{{ c.desc }}</td>
                  <td>{{ c.args.join(" ") }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="block">
            <h2>컴포넌트 명령어</h2>
            <table class="table table-bordered">
              <thead>
                <tr>
                  <th>Command</th>
                  <th>Description</th>
                  <th>Arguments</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(c, idx) in commands.node" :key="idx">
                  <td>{{ c.cmd }}</td>
                  <td>{{ c.desc }}</td>
                  <td>{{ c.args.join(" ") }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </article>
    <footer>
      <div class="wrapper">
        <a class="brand" href="/">Publessing</a>
        <div class="copyright">&copy; 2021. Publessing. All rights reserved.</div>
      </div>
    </footer>
  </div>
</template>
<script>
import { onMounted, reactive, ref } from "@vue/runtime-core";
import defines from "../defines";
import $ from "jquery";

export default {
  props: {
    login: Boolean,
    visible: Boolean,
    callback: Function,
  },
  setup() {
    const rootRef = ref();
    const state = reactive({
      navs: [
        { name: "home", active: true },
        { name: "guide", active: false },
        { name: "intro", active: false },
        { name: "contact", active: false },
      ],
    });

    const definedCommands = defines.commands;

    const commands = {
      console: [],
      node: [],
    };

    const scrollTops = {
      guide: 0,
    };

    const move = (name) => {
      let scrollTop = 0;
      state.navs.forEach((x) => (x.active = false));
      state.navs.find((x) => x.name === name).active = true;

      switch (name) {
        case "guide":
          scrollTop = scrollTops.guide;
          break;
      }

      $(rootRef.value)
        .stop()
        .animate({
          scrollTop,
        });
    };

    for (let i1 in definedCommands) {
      for (let i2 in definedCommands[i1]) {
        for (let c of definedCommands[i1][i2]) {
          commands[i1].push(c);
        }
      }
    }

    for (let i in commands) {
      commands[i].sort((a, b) => {
        if (a.cmd < b.cmd) {
          return -1;
        } else if (a.cmd > b.cmd) {
          return 1;
        }

        return 0;
      });
    }

    onMounted(() => {
      let timer;
      const navHeight = rootRef.value.querySelector("nav").offsetHeight;
      scrollTops.guide = rootRef.value.querySelector("article > .guide").offsetTop - navHeight + 1;

      rootRef.value.addEventListener("scroll", function() {
        clearTimeout(timer);

        timer = setTimeout(() => {
          state.navs.forEach((x) => (x.active = false));

          if (rootRef.value.scrollTop >= scrollTops.guide) {
            state.navs.find((x) => x.name === "guide").active = true;
          } else {
            state.navs.find((x) => x.name === "home").active = true;
          }
        }, 25);
      });
    });

    return { rootRef, state, move, commands };
  },
};
</script>
<style lang="scss" scoped>
.component-home {
  height: 100%;
  overflow: auto;
  position: relative;

  .wrapper {
    width: 1140px;
    margin: 0 auto;
    max-width: 100%;
    padding: 0 15px;
  }

  > nav {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #ddd;
    background: #f7f7f7;
    z-index: 1;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        cursor: pointer;
        display: inline-block;
        padding: 19px 10px;
        margin: 0 0 0 15px;

        &.active {
          font-weight: bold;
        }
      }
    }

    button {
      position: absolute;
      border-radius: 0;
      top: 6.5px;
      right: 7px;
      box-shadow: none;
      border: 1px solid #146ece;
      background: none;
      color: #146ece;
      padding: 12.5px 0;
      width: 150px;
      font-size: 14px;

      &:hover {
        background: #146ece;
        color: #fff;
      }
    }
  }

  > header {
    width: 100%;
    height: 100%;
    max-height: 1080px;
    display: table;
    margin-top: -55px;
    position: relative;
    text-align: center;

    > .inner {
      display: table-cell;
      vertical-align: middle;

      > img {
        height: 100px;
        margin-bottom: 42px;
      }

      > h1 {
        font-size: 80px;
        font-weight: bold;
        display: inline-block;
        position: relative;
        margin-bottom: 25px;
      }

      > h2,
      > h3 {
        font-size: 28px;
      }

      h2 {
        margin-bottom: 60px;
      }

      > h3 {
        line-height: 45px;
      }

      > button {
        margin-top: 50px;
        width: 300px;
        height: 60px;
        line-height: 60px;
        font-size: 18px;
        padding: 0;
        box-shadow: none;
        outline: 0;
        border-radius: 0;
      }
    }
  }

  > article {
    min-height: 100%;
    border-top: 1px solid #ddd;

    > .wrapper {
      padding-top: 50px;
      padding-bottom: 25px;

      > .title {
        font-size: 24px;
        font-weight: bold;
      }

      > .content {
        h1 {
          font-size: 18px;
          font-weight: 600;
          padding-bottom: 20px;
          margin-top: 35px;
          margin-bottom: 20px;
          border-bottom: 1px solid #ddd;
        }

        h2 {
          font-size: 16px;
        }

        ul {
          padding-bottom: 15px;

          li {
            margin-bottom: 5px;
          }
        }

        .block {
          padding-left: 25px;
          margin-bottom: 15px;
          padding-top: 5px;
          padding-bottom: 5px;

          table {
            margin-top: 20px;

            th {
              background: #f3f7fb;
              border-bottom-width: 1px;
            }
          }
        }
      }
    }
  }

  > footer {
    border-top: 1px solid #ddd;
    padding: 30px 0;

    > .wrapper:after {
      content: "";
      clear: both;
      display: table;
    }

    .brand {
      float: left;
      color: #1259a7;
      cursor: pointer;
      font-family: "Parisienne", cursive;
      font-size: 42px;
      font-weight: bold;
      padding-right: 50px;
      text-decoration: none;
    }

    .copyright {
      padding: 17.5px 0;
      float: left;
    }
  }
}
</style>
